@import "./news.less";

@media (min-width: 1200px){
  .container {
    width: 1220px;
  }
}
a{
  color: #F79B12;
}
a:hover,a:active,a:link,a:visited{
  text-decoration: none;
  color: #F79B12;
}
.navbar-default .navbar-nav > .active > a, .navbar-default .navbar-nav > .active > a:focus, .navbar-default .navbar-nav > .active > a:hover,
.navbar-default .navbar-nav > .open > a, .navbar-default .navbar-nav > .open > a:focus, .navbar-default .navbar-nav > .open > a:hover {
  color: #F79B12;
  background: none;
}
.navbar-toggle {
  border: 1px solid #ccc;
  .icon-bar{
    background:#ccc
  }
}
.navbar{
  width: 100%
}
.navbar-default .navbar-nav > li > a {
  color: #333333;
  line-height: 50px;
}.navbar-brand {
   width: 140px;
   background-repeat: no-repeat;
   background-position: center;
   -webkit-background-size: 80%;
   background-size: 80%;
   background-image: url("https://img.datuodui.com/ws/logo.png");
 }
.bs-docs-nav .navbar-brand, .bs-docs-nav .navbar-nav>li>a{
  color: #333333;
}
.navbar-nav>li.open>a{
  .glyphicon{
    transform:rotate(180deg);
  }
}
.navbar-nav>li>a{
  font-size: 16px;
  .glyphicon{
    transition:0.1s;
  }
}
.navbar-right.login-nav{
  li>a{
    padding: 6px;
    margin: 9px 0 0 6px;
    &.primary-link{
      color: #fff;
      padding: 6px 20px;
      background: #F79B12;
    }
  }
}
.fl{
  float: left;
}
.dropdown-menu{
  border-radius: 0;
  border-top: 0;
  background: #eeeeee;
  padding: 20px;
  li{
    line-height: 40px;
    font-size: 14px;
    padding: 0 10px;
    width: 200px;
    transition: all 0.3s;
    a,a:hover{
      color: inherit;
      background: transparent;
    }
    &:hover{
      background: #F79B12;
      color: #fff;
    }
  }
}
.index-block {
  padding: 52px 0;
  .block-title {
    text-align: center;
    font-size: 34px;
    color: #000000;
  }
  .block-subtitle {
    text-align: center;
    font-size: 16px;
    margin-bottom: 30px;
    color: #999999;
  }
}
.footer-top{
  padding-left: 20px;
}
.w-block {
  background: #ffffff;
}

.b-block {
  background: rgb(242, 244, 244);
}
.last-block{
  background: #323232;
  color: #fff;
  .block-subtitle,.block-title{
    color: #fff;
  }
}

.navbar {
  background: #fff;
  margin: 0;
  border-bottom: 1px solid #ccc;
}

.yellow-span {
  color: #F79B12;
}

.data-row {
  background: #FAFAFA;
  padding: 10px 0;
  font-size: 24px;
  .data-col {
    margin: 11px 0;
    text-align: center;
  }
}

.jumbotron {
  margin: 0;
  padding: 0;
  img {
    width: 100%;
  }
  .header{
    height: 300px;
    padding-top: 100px;
    background-position: center top;
  }
  .try-btn{
    font-weight: normal;
    background: transparent;
    width: 80px;
    background: transparent;
    border:2px solid #fff;
    color: #fff;
    padding: 11px 18px;
    font-size: 16px;
    line-height: 20px;
    transition:border .3s,color .3s,background-color .3s;
    &.gray{
      background: #9c9c9c;
    }
    &:hover{
      background: #fff;
      border-color: #fff;
      color: #000;
    }
  }
}


.bot-lit-block{
  padding: 15px;
  text-align: center;
}
.litBlock {
  text-align: center;
  margin-top: 30px;
  height: 230px;
  border-radius: 10px;
  padding: 20px 16px;
  transition:background 0.3s;
  box-shadow: 0px 1px 10px 1px #d6d6d6;
  .nor{
    display: inline-block;
  }
  .over{
    display: none;
  }
  &:hover{
    box-shadow:none;
    background:#F79B12 ;
    color: #fff;
    .nor{
      display: none;
    }
    .over{
      display: inline-block;
    }
  }
  .pic-icon {
    margin: 0 auto;
    border-radius: 100px;
    width: 100px;
    height: 100px;
  }
  .title {
    font-size: 24px;
  }
  .subtitle {
    font-size: 14px;
  }
}
.anli-img {
  margin-bottom: 28px;
  img {
    width: 100%;
  }
}
.hangye-block{
  margin-bottom: 28px;
}
.bot-list{
  a{
    margin-right: 30px;
  }
}
.shu-pic {
  position: relative;
  overflow: hidden;
  img {
    width: 100%;
  }
  transition:all 0.3s;
  &:hover{
    box-shadow: 2px 2px 10px 1px #5d5d5d;
    margin-top: -2px;
    .img-title {
      height: 300px;
    }
  }
  .img-title {
    width: 100%;
    height: 250px;
    bottom: 0;
    padding: 10px 14px;
    position: absolute;
    background-color: #fff;
    .title{
      font-size: 24px;
      margin-bottom: 15px;
    }
    .content{
      font-size: 16px;
      margin-bottom: 15px;
    }
  }
}

.container .content{
  background: #ffffff;
  h2{
    text-align: center;
    margin-bottom: 43px;
  }
  .c-block{
    padding: 40px 0;
  }
  .option-row{
    line-height: 50px;
    span{
      margin-right: 24px;
      height: 24px;
      font-size: 18px;
      padding-left: 30px;
      background: url(https://img.datuodui.com/ws/content-icon.png);
      background-repeat: no-repeat;
      line-height: 24px;
      display: inline-block;
      background-position: 0 0;
    }
  }
  .c-img{
    text-align: center;
  }
  img{
    margin-top: 70px;
    margin-bottom: 70px;
    max-width: 100%;
  }
}
.carousel {
  .img-inner {
    .container {
      height: 500px;
    }

    .title {
      font-size: 26px;
      text-shadow: 0px 0px 9px rgba(0, 0, 0, 0.39);
    }
    .subtitle {
      font-size: 38px;
      font-weight: bold;
      text-shadow: 0px 0px 9px rgba(0, 0, 0, 0.39);
    }
    padding: 100px 40px;
    color: #ffffff;
    height: 500px;
    vertical-align: middle;
  }
}
.footer{
  background-color: #f4f4f4;
  padding-top: 20px;
  hr{
    border-color: #cacaca;
  }
}
.footer a{
  color: #000;
}
#can{
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
